@media (max-width: 767px) {
  [data-header="header"] nav > ul {
    transition: all .3s;
  }

  [data-header="header"] nav > ul li {
    transform: translateX(-10px);
    transition-duration: .2s !important;
  }

  [data-header="header"] [data-header="btn"] {
    aspect-ratio: 1 / 1;
    align-items: center;
    display: flex;
  }

  [data-header="header"] [data-header="btn"] span {
    height: 2px;
    width: 25px;
    background-color: #fff;
  }

  [data-header="header"] [data-header="btn"] span:before, [data-header="header"] [data-header="btn"] span:after {
    content: "";
    width: 25px;
    height: 2px;
    transform-origin: center;
    background-color: #fff;
    transition: all .3s;
    display: block;
  }

  [data-header="header"] [data-header="btn"] span:before {
    margin-top: -7px;
  }

  [data-header="header"] [data-header="btn"] span:after {
    margin-top: 13px;
  }

  [data-header="header"].opened [data-header="btn"] span {
    background-color: #0000;
  }

  [data-header="header"].opened [data-header="btn"] span:before {
    margin-top: 0;
    transform: rotate(45deg);
  }

  [data-header="header"].opened [data-header="btn"] span:after {
    margin-top: -1px;
    transform: rotate(-45deg)translateY(-1px);
  }

  [data-header="header"].opened nav > ul {
    height: calc(100vh - 64px);
    padding-block: 1.5rem;
  }

  [data-header="header"].opened nav > ul li {
    opacity: 1;
    transform: translateX(0);
  }

  [data-header="header"].opened nav > ul li:nth-child(1) {
    transition-delay: .2s;
  }

  [data-header="header"].opened nav > ul li:nth-child(2) {
    transition-delay: .3s;
  }

  [data-header="header"].opened nav > ul li:nth-child(3) {
    transition-delay: .4s;
  }

  [data-header="header"].opened nav > ul li:nth-child(4) {
    transition-delay: .5s;
  }
}

